import "./goods_common.css";
import "./goods_custom.css";
import "./goods.css";
import "./goods_theme.css";
import "./goods_mars.css";
import "./goods_sku.css";

import Vue from 'vue';
import url from 'js/api.js';
import axios from 'axios';
import {loadPageVar} from 'js/common.js';
import { InfiniteScroll } from 'mint-ui';
import Swipe from 'components/Swipe.vue';
Vue.use(InfiniteScroll);

new Vue({
  el: '#app',
  components: {Swipe},
  data: {
    detail: null,
    imageList: [],
    skuShow: false,
    skuIndex: 0,
    quantity: 1,
    loading: false,
    recommendList: null,
    cartShow: false,
    tipsShow: false,
    dealShow: false,
    deal: null,
    id: null,
  },
  created() {
    this.id = loadPageVar('id');
    this.getDetail();
    this.getHotList();
    // this.skuShow = true;
  },
  watch: {
    skuShow(newVal, oldVal) {
      if(newVal){
        document.body.style.overflowY = 'hidden';
      } else {
        document.body.style.overflowY = 'auto';
      }
    },
    quantity(newVal, oldVal) {
      if(newVal < 1){
        this.quantity = 1;
      } else if(newVal > 9) {
        this.quantity = 9;
      }
    }
  },
  methods: {
    getDetail() {
      axios.get(url.detail, {id: this.id}).then(res => {
        this.detail = res.data.data;
        let imgs = res.data.data.imgs;
        for (let val of imgs) {
          this.imageList.push({img: val});
        }
      })
    },
    getHotList() {
      axios.get(url.recommendList, {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      }).then(response => {
        this.recommendList = response.data.list;
      })
    },
    changeSkuIndex(index) {
      this.skuIndex = parseInt(index);
    },
    addCart() {
      this.tipsShow = true;
      setTimeout(() => {
        this.tipsShow = false;
      },500);
      this.cartShow = true;
    },
    showDeal() {
      if(this.deal){ this.dealShow = true} else {
        axios.get(url.deal,{id: this.id}).then(res => {
          this.deal = res.data.data.lists;
          this.dealShow = true;
        })
      };
    },
  },
})


