<template>
  <mt-swipe :auto="3000" class="swipe-container">
    <mt-swipe-item v-for="(item,index) in list" :key="index">
      <img :src="item.img" alt="banner">
    </mt-swipe-item>
  </mt-swipe>
</template>

<script>
import { Swipe, SwipeItem } from 'mint-ui';
import Vue from 'vue';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
export default {
  name: 'swipe',
  props: ['list']
}
</script>

<style scoped>
  /* .swipe-container {
    width: 100vw;
    height: 48vw;
  } */
  .swipe-container img{
    width: 100%;
    height: 100%;
  }
</style>



